body {
  background-color: #85929e;
  box-sizing: border-box;
  margin: 0;
  padding: 0%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

div {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -30px;
}

h1 {
  padding: 20px 20px;
  background-color: #ffe9c5;
  color: #e74c3c;
  border-radius: 20px;
}

span {
  color: #e74c3c;
}
